import '../style/Control.css';
import { useState,useEffect } from 'react';
// import { Link } from 'react-router-dom';
import MyDrawer from '../Drawer';
import LoginPage from './DoctorLogin';

function DoctorHome({socket}) {
  const username = "Yu-Cheng Wang"; 
  const [processData,setProcessData] = useState({
    person: "Yu-Cheng Wang",
    login: false,
    password : "escapebfyj2022",
    doorlock: false
  });

  useEffect(()=>{
    socket.emit("get_process_data",{user:username});
  },[])
  useEffect(() => {
    socket.on("process_data",(data)=>{
        setProcessData(data);
    })
  }, [socket]);

  const newWin1 = () =>{
    // const w = window.open('about:blank');
    // window.location.href='/sixpin00/Cho';
    window.location.href='/sixpin00/Pub';

  };
  const newWin2 = () =>{
    // const w = window.open('about:blank');
    window.location.href='/sixpin00/Lock';
  };

  return (
    <div>
      <MyDrawer Login = {processData.login}/>
      {processData.login?
      (<div className='controlCenter'>
        <h2></h2>
        <h3>{" "}</h3>
        <button onClick = {newWin1}></button>
        <button onClick = {newWin2}></button>
        {/*  */}
        {/* <h3></h3> */}
        
      </div>):
      (<div><LoginPage socket = {socket}/></div>)}
      
    </div>);
}

export default DoctorHome;





