// import './App.css';
import { useState,useEffect } from 'react';
// import { Link } from 'react-router-dom';
import MyDrawer from '../Drawer';
import "../style/Lock.css"
// import Switch from "@mui/material/Switch";
import NotLogin from './NotLogin'


function DoorLock({socket}) {
  const username = "Yu-Cheng Wang"
  const [lock,setLock] = useState(false);
  const [password,setPassword] = useState("jdoisajdla");
  const [login,setLogin] = useState(false);

  useEffect(()=>{
    socket.emit("get_process_data",{user:username});
  },[])
  useEffect(() => {
    socket.on("process_data",(data)=>{
    
        setLogin(data.login);
        setPassword(data.password);
        setLock(data.doorlock);
    })
  }, [socket]);
  const button_Lock = () =>{
    setLock(true);
    socket.emit("set_process_data",{info:{
      person: "Yu-Cheng Wang",
      login: login,
      password : password,
      doorlock: true,
    }});
  }

  return (
    <div>
      <MyDrawer Login = {login}/>
      {login?(<div>
        {lock?(<div className='Locked'></div>):
          (<div className='LockButton'>
            <img  src = "/room.png" alt="ohNo"/>
            <button onClick = {button_Lock}>
              <p className='LockP'></p>
            </button>
          </div>)}
      </div>):<NotLogin/>}
      
      
       
      
    </div>
        
      
          
  );
}

export default DoorLock;
