import './style/Drawer.css';
import * as React from 'react';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import Drawer from '@mui/material/Drawer'; 
import Box from '@mui/material/Box';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';


function MyDrawer(props) {
    const [draw,setDraw] = useState(false);
    const toggleDrawer = (open) => (event) => {
      if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
        return;
      }
      setDraw(open);
    };
    const list = props.Login?(
      <Box
        sx= {{width:250}} 
        role="presentation"
        onClick={toggleDrawer(false)}
        onKeyDown={toggleDrawer(false)}
      >
        <List>
          {/* {['', '',''].map((text) => (
            <ListItem key={text} disablePadding>
              <ListItemButton LinkComponent={Link} to='/'>
                <ListItemText primary={text} />
              </ListItemButton>
            </ListItem>
          ))} */}
          <ListItem key={''} disablePadding>
            <ListItemButton LinkComponent={Link} to='/sixpin00'>
              <ListItemText primary={''} />
            </ListItemButton>
          </ListItem>
          <ListItem key={''} disablePadding>
            <ListItemButton LinkComponent={Link} to='/sixpin00/Pri'>
              <ListItemText primary={''} />
            </ListItemButton>
          </ListItem>
          <ListItem key={''} disablePadding>
            <ListItemButton LinkComponent={Link} to='/sixpin00/Pub'>
              <ListItemText primary={''} />
            </ListItemButton>
          </ListItem>
          <ListItem key={''} disablePadding>
            <ListItemButton LinkComponent={Link} to='/sixpin00/Lock'>
              <ListItemText primary={''} />
            </ListItemButton>
          </ListItem>
          <ListItem key={''} disablePadding>
            <ListItemButton LinkComponent={Link} to='/sixpin00/Info'>
              <ListItemText primary={''} />
            </ListItemButton>
          </ListItem>
        </List>
        <Divider />
      </Box>
    ):(
      <Box
        sx= {{width:250}} 
        role="presentation"
        onClick={toggleDrawer(false)}
        onKeyDown={toggleDrawer(false)}
      >
        <List>
          {/* {['',''].map((text) => (
            <ListItem key={text} disablePadding>
              <ListItemButton LinkComponent={Link} to='/'>
                <ListItemText primary={text} />
              </ListItemButton>
            </ListItem>
          ))} */}
          <ListItem key={''} disablePadding>
              <ListItemButton LinkComponent={Link} to='/sixpin00'>
                <ListItemText primary={''} />
              </ListItemButton>
            </ListItem>
          <ListItem key={''} disablePadding>
            <ListItemButton LinkComponent={Link} to='/sixpin00/info'>
              <ListItemText primary={''} />
            </ListItemButton>
          </ListItem>
        </List>
        <Divider />
      </Box>
    );

  
    return (
      <header>
        <nav>
          <div className='toolbar'>
            {/* <h2></h2>
            <Switch/> Lock the Door  */}
            {props.Login?(<div><a href = '/sixpin00'>,Taipei City Songjiang Hospital</a></div>):(<div><a href = '/sixpin00'></a></div>)}
             <div>
                <button onClick={toggleDrawer(true)}>
                  <div className='button__line'></div>
                  <div className='button__line'></div>
                  <div className='button__line'></div>
                </button>
              </div>
                <Drawer
                  anchor='right'
                  open={draw}
                  onClose={toggleDrawer(false)}
                >{list}
                </Drawer> 
            
          </div> 
        </nav>
        <div className='footer'>
          <p>cycle</p>
        </div>
      </header>     
    );
}

export default MyDrawer;
