// import io from 'socket.io-client';
import '../style/Chat.css';
import { useState,useEffect } from 'react';
// import Chat from './Chat';
import MyDrawer from '../Drawer';

//

function DoctorLogin ({socket}) {

  const [processData,setProcessData] = useState({
    person: "Yu-Cheng Wang",
    login: false,
    password : "escapebfyj2022",
    doorlock: false
  });
  
  const [password, setPassword] = useState('');

  useEffect(()=>{
    socket.emit("get_process_data",{user:"Yu-Cheng Wang"});
  },[])

  useEffect(() => {
    socket.on("process_data",(data)=>{
        setProcessData(data);
    })
  }, [socket]);
  


  const joinRoom = async() =>{
    if (password === processData.password){

        setProcessData({
          person: "Yu-Cheng Wang",
          login: true,
          password : processData.password,
          doorlock: processData.doorlock,
        }
        )
        await socket.emit("set_process_data",{info:{
          person: "Yu-Cheng Wang",
          login: true,
          password : processData.password,
          doorlock: processData.doorlock,
        }});
        window.location.href='/sixpin00';// 
    }
    else alert("wrong input");
  };

  const forgetPassword = async () =>{
    // alert("hint: Your pet's name. \n (hint: the password is password)");
    var petName = prompt("What's your pet's name?");
    if (petName === "Shalom"){//pet name is Shalom
      var newPassword = prompt('Enter new password: (at least 6 letter)');
      if(newPassword.length>5 && newPassword === prompt('Re-type new password:')){
        setProcessData({
          person: "Yu-Cheng Wang",
          login: processData.login,
          password :newPassword,
          doorlock: processData.doorlock,
        }
        )
        await socket.emit("set_process_data",{info:{
          person: "Yu-Cheng Wang",
          login: processData.login,
          password :newPassword,
          doorlock: processData.doorlock,
        }});
      }
      else alert("please try again.")
    }
    else alert("wrong input");
  }

  return (
    <div>
        {/* <MyDrawer Login = {true}/> */}
        <div className="App">
        <div className='joinChatContainer'>
            <h3>Welcome</h3>
            <input type="password" placeholder='Password' onChange = {(event) => {setPassword(event.target.value);}} onKeyPress = {(event) => {event.key === "Enter" && joinRoom()}}/>
            <button onClick = {joinRoom} onKeyPress = {(event) => {event.key === "Enter" && joinRoom()}}>Login</button>
            <button onClick = {forgetPassword}>Forget password</button>
        </div>
        </div>
    </div>
  );
}

export default DoctorLogin;
