// import './App.css';
import { useState,useEffect } from 'react';
import { Link } from 'react-router-dom';
import MyDrawer from '../Drawer.js';
import '../style/HomePage.css';


function DocInfo({socket}) {
    const username = "Yu-Cheng Wang"
    const [login,setLogin] = useState(false);
  
    useEffect(()=>{
      socket.emit("get_process_data",{user:username});
    },[])
    useEffect(() => {
      socket.on("process_data",(data)=>{
          setLogin(data.login);
      })
    }, [socket]);

    return (
        <div className='InfoPage'>
            <MyDrawer Login = {login}/>
            <h1></h1>
        </div>    
    );
}

export default DocInfo;
