// import io from 'socket.io-client';
import '../style/Chat.css';
import { useState,useEffect } from 'react';
import Chat from '../Chat';
import MyDrawer from '../Drawer';
import NotLogin from './NotLogin';
//


// const socket = io.connect("http://localhost:5000");
//web_change 1/4
// const socket = io.connect("https://cycle-escape2022.onrender.com");

function DoctorChatPage({socket,room,otheruser}) {
  const username = "Yu-Cheng Wang"
  // const [lock,setLock] = useState(false);
  // const [password,setPassword] = useState("jdoisajdla");
  const [login,setLogin] = useState(false);

  useEffect(()=>{
    socket.emit("get_process_data",{user:username});
  },[])
  useEffect(() => {
    socket.on("process_data",(data)=>{
        setLogin(data.login);
    })
  }, [socket]);
  
  socket.emit("join_room", room);
  
  return (
    <div>
      {login?(<div>
        <MyDrawer Login = {login}/>
        <div className="App">          
          <Chat socket = {socket} username = {" "} room = {room} otheruser = {otheruser}/>
        </div>
      </div>):<NotLogin/>}
      
    </div>
  );
}

export default DoctorChatPage;
